.list {
  position: relative;
  width: 100%;
}

.list-wrapper {
  height: 100vh;
  overflow: auto;
}

.list-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: inherit;
}

.list-item:hover {
  color: inherit;
  text-decoration: none;
}

.list-item-border {
  border-bottom: 1px solid var(--gray);
}

.hide-overflow {
  height: 100%;
  overflow: hidden;
}